a{
  color: rgb(253, 253, 253);
  text-decoration: none;
  border: none;
  padding: 0%;
  margin: 0%;
  cursor: pointer;
}

a:hover{
  text-decoration: underline;
}

.a{
  text-decoration: none;
  color: white;
}

.about-desc{
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 500;
}

.about-head{
  margin-bottom: 15px;
  display: block;
  font-size: 10px;
  text-transform: uppercase;
  color: #999999;
  font-weight: 500;
  letter-spacing: 5px;
}

.about-heading{
  font-size: 18px;
  margin-bottom: 50x;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 5px;
  line-height: 1.8;
  position: relative;
}

.about-last{
  background-color: rgb(252, 229, 154);
  padding: 20px;
  padding-top: 0px;
  border-radius: 8px;
}

.about-last button{
  background-color: rgb(9, 6, 184);
  padding: 9px;
  text-decoration: none;
  color: white;
  border: none;
  outline: none;
  border-radius: 5px;

}

.about-last h2{
  font-family: cursive;
  font-size: 150%;
}

.about-last h2 span{
  color: red;
}

.accordion{
  background-color: rgb(173, 235, 250);
  color: #070707;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  letter-spacing: 5px;
  font-variant-caps: all-small-caps;
}

.accordion:after{
  content: '\02795';
  font-size: 13px;
  float: right;
  margin-left: 5px;
}

.accordion-style{
  width: 80%;
  margin-top: 80px;
  margin-left: 80px;
  box-shadow: 0px 0px 20px rgb(221, 8, 168)
}

.active, .accordion:hover{
  background-color: #402cf0;
}

.active:after{
  content: '\2796'
}


